<script setup lang="ts">
import iconDownload from '@/assets/img/download.png'
import logoLight from '@/assets/img/logo-light.png'
import { useAuthStore } from '@/stores/auth'
import { useUiStore } from '@/stores/ui'
import { ElMessage } from 'element-plus'
import { computed, onMounted, onUnmounted, ref, watchEffect } from 'vue'
import { RouterLink, useRoute, useRouter } from 'vue-router'
import { useMallRedirect } from '@/utils/mallNavigation'

// Logo URL
const logoUrl = logoLight
const iconDownloadUrl = iconDownload

// 
interface NavItem {
  to: string
  label: string
  children?: NavItem[]
}

const open = ref(false)
const openMobileSubmenu = ref<string | null>(null)
const activeDropdown = ref<string | null>(null)
const userDropdownOpen = ref(false)
const mobileUserPanelOpen = ref(false)
const route = useRoute()
const router = useRouter()
const ui = useUiStore()
const auth = useAuthStore()
const { redirectToMall } = useMallRedirect()

const onClickSupplierConsole = () => {
  redirectToMall()
}

// TopAdv
const isTopAdvVisible = ref(false)

// top
const mobileMenuTopClass = computed(() => {
  // h-20 = 80px
  // TopAdv h-11 = 44pxPC h-[66px] = 66px
  const navHeight = 80 // 
  const topAdvHeight = isTopAdvVisible.value ? 44 : 0 // TopAdv

  return {
    'top-20': !isTopAdvVisible.value, // TopAdvtop-20
    'top-[124px]': isTopAdvVisible.value, // TopAdv80px + 44px = 124px
  }
})

// top
const mobileUserPanelTopClass = computed(() => {
  // h-20 = 80px
  // TopAdv h-11 = 44px
  const navHeight = 80 // 
  const topAdvHeight = isTopAdvVisible.value ? 44 : 0 // TopAdv

  return {
    'top-20': !isTopAdvVisible.value, // TopAdvtop-20
    'top-[124px]': isTopAdvVisible.value, // TopAdv80px + 44px = 124px
  }
})

// 
let closeTimer: number | null = null

// TopAdv
const checkTopAdvVisibility = () => {
  // TopAdv
  const topAdvElement =
    document.querySelector('[data-top-adv]') ||
    document.querySelector('.bg-gradient-to-r.from-orange-100.to-orange-200')

  if (topAdvElement) {
    const rect = topAdvElement.getBoundingClientRect()
    const isVisible = rect.height > 0 && rect.top >= 0
    isTopAdvVisible.value = isVisible
  } else {
    isTopAdvVisible.value = false
  }
}

// 
onMounted(() => {
  checkTopAdvVisibility()
  // MutationObserverDOM
  const observer = new MutationObserver(checkTopAdvVisibility)
  observer.observe(document.body, {
    childList: true,
    subtree: true,
    attributes: true,
    attributeFilter: ['class', 'style'],
  })

  // 
  window.addEventListener('resize', checkTopAdvVisibility)

  // 
  onUnmounted(() => {
    observer.disconnect()
    window.removeEventListener('resize', checkTopAdvVisibility)
  })
})

// 
watchEffect(() => {
  route.path
  open.value = false
  openMobileSubmenu.value = null
  activeDropdown.value = null
  userDropdownOpen.value = false
  mobileUserPanelOpen.value = false
  if (closeTimer) {
    clearTimeout(closeTimer)
    closeTimer = null
  }
})

// 
const isActive = (path: string) => {
  return route.path === path
}

// 
const isActiveParent = (item: NavItem) => {
  if (!item.children) return false
  return item.children.some(
    (child) => route.path === child.to || route.path.startsWith(child.to + '/')
  )
}

// 
const toggleMobileSubmenu = (path: string) => {
  openMobileSubmenu.value = openMobileSubmenu.value === path ? null : path
}

// PC
const handleMouseEnter = (path: string) => {
  // 
  if (closeTimer) {
    clearTimeout(closeTimer)
    closeTimer = null
  }
  // 
  activeDropdown.value = path
  userDropdownOpen.value = false
}

// 
const handleDropdownEnter = () => {
  // 
  if (closeTimer) {
    clearTimeout(closeTimer)
    closeTimer = null
  }
  // 
  userDropdownOpen.value = false
}

// 
const handleMouseLeave = () => {
  // 150ms
  closeTimer = window.setTimeout(() => {
    activeDropdown.value = null
    closeTimer = null
  }, 150)
}

// PC
const handleUserMouseEnter = () => {
  if (closeTimer) {
    clearTimeout(closeTimer)
    closeTimer = null
  }
  // 
  userDropdownOpen.value = true
  activeDropdown.value = null
}

const handleUserMouseLeave = () => {
  // 
  closeTimer = window.setTimeout(() => {
    userDropdownOpen.value = false
    closeTimer = null
  }, 150)
}

// 
const handleLogout = () => {
