import NProgress from 'nprogress'
import 'nprogress/nprogress.css'
import { createRouter, createWebHistory, RouteRecordRaw } from 'vue-router'

const routes: RouteRecordRaw[] = [
  // 
  {
    path: '/',
    name: 'Home',
    component: () => import('@/pages/Home/Home.vue'),
    meta: { title: '' },
  },

  // 
  {
    path: '/products',
    name: 'Products',
    component: () => import('@/pages/Products.vue'),
    meta: { title: '' },
    children: [
      {
        path: 'mini-program',
        name: 'ProductMiniProgram',
        component: () => import('@/pages/Products/MiniProgram.vue'),
        meta: { title: 'AI ' },
      },
      {
        path: 'app',
        name: 'ProductApp',
        component: () => import('@/pages/Products/App.vue'),
        meta: { title: 'AI App' },
      },
      {
        path: 'supplier-console',
        name: 'ProductSupplierConsole',
        component: () => import('@/pages/Products/SupplierConsole.vue'),
        meta: { title: '' },
      },
      {
        path: 'mall',
        name: 'ProductMall',
        component: () => import('@/pages/Products/Mall.vue'),
        meta: { title: '' },
      },
    ],
  },

  // 
  {
    path: '/cooperation',
    name: 'Cooperation',
    component: () => import('@/pages/Cooperation.vue'),
    meta: { title: '' },
    children: [
      {
        path: 'suppliers',
        name: 'CooperationSuppliers',
        component: () => import('@/pages/Cooperation/Suppliers.vue'),
        meta: { title: '' },
      },
      {
        path: 'sellers',
        name: 'CooperationSellers',
        component: () => import('@/pages/Cooperation/Sellers.vue'),
        meta: { title: 'AI' },
      },
      {
        path: 'partners',
        name: 'CooperationPartners',
        component: () => import('@/pages/Cooperation/Partners.vue'),
        meta: { title: '' },
      },
    ],
  },

  // 
  {
    path: '/about',
    name: 'About',
    component: () => import('@/pages/About.vue'),
    meta: { title: '' },
    children: [
      {
        path: 'company',
        name: 'AboutCompany',
        component: () => import('@/pages/About/Company.vue'),
        meta: { title: 'AI' },
      },
      {
        path: 'careers',
        name: 'AboutCareers',
        component: () => import('@/pages/About/Careers.vue'),
        meta: { title: '' },
      },
      {
        path: 'contact',
        name: 'AboutContact',
        component: () => import('@/pages/Contact.vue'),
        meta: { title: '' },
      },
    ],
  },

  // 
  {
    path: '/legal',
    name: 'Legal',
    component: () => import('@/pages/Legal.vue'),
    meta: { title: '' },
    children: [
      {
        path: 'help',
        name: 'HelpCenter',
        component: () => import('@/pages/Legal/Help.vue'),
        meta: { title: '' },
      },
      {
        path: 'privacy',
        name: 'PrivacyPolicy',
        component: () => import('@/pages/Legal/Privacy.vue'),
        meta: { title: '' },
      },
      {
        path: 'terms',
        name: 'TermsOfService',
        component: () => import('@/pages/Legal/Terms.vue'),
        meta: { title: '' },
      },
    ],
  },

  // 

  // 
  { path: '/:pathMatch(.*)*', redirect: '/' },
]

const router = createRouter({
  history: createWebHistory(),
  routes,
  scrollBehavior() {
    return { top: 0 }
  },
})

NProgress.configure({ showSpinner: false })

router.beforeEach(() => {
  NProgress.start()
  return true
})

router.afterEach((to) => {
  if (to.meta?.title) {
    document.title = String(to.meta.title) + ' - '
  }
  NProgress.done()
})

export default router
